import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

@Injectable()
export class FriendsService {

  constructor(private http: Http) { }
  getFriendsAfter(Token: string, cursors: string) {
    const url = 'https://graph.facebook.com/v3.0/me/friends?limit=10&fields=id,name,gender,email,username,friends.limit(0)&access_token=' + Token + '&after=' + cursors;

    return this.http.get(url)
      .toPromise()
      .then(res => res.json());
  }

  getFriendsBefore(Token: string, cursors: string) {
    const url = 'https://graph.facebook.com/v3.0/me/friends?limit=10&fields=id,name,gender,email,username,friends.limit(0)&access_token=' + Token + '&before=' + cursors;

    return this.http.get(url)
      .toPromise()
      .then(res => res.json());
  }

}
