import { Component, OnInit } from '@angular/core';
import { InboxReaderService } from './inbox-reader.service';
import * as $ from 'jquery';
import { HtmlParser } from '@angular/compiler';

@Component({
  selector: 'app-inbox-reader',
  templateUrl: './inbox-reader.component.html',
  styleUrls: ['./inbox-reader.component.css']
})
export class InboxReaderComponent implements OnInit {
  token = '';
  data: any;
  data_next: string;
  paging_next: string;
  myName: string;
  messages: any;
  trangthai = false;
  idclick: string;
  rank = [];
  rank_next = 'mducptit';

  constructor(private inboxReaderService: InboxReaderService) { }

  ngOnInit() {
  }

  getListChat() {
    this.inboxReaderService.getListChat(this.token)
      .then(res => {
        // console.log(res);
        this.data_next = res.paging.next;
        console.log(this.data_next);
        res = res.data;
        this.data = res;
        // this.inboxReaderService.checkInfo(this.token)
        //   .then(res => {
        //     this.myName = res.name;
        //     console.log(this.myName);
        //   });
      })
      .catch(err => console.log(err));
  }

  getListChatMore() {
    this.inboxReaderService.getListChatMore(this.data_next)
      .then(res => {
        this.data_next = res.paging.next;
        let tmp = res.data;
        this.data = this.data.concat(tmp);
        // this.inboxReaderService.checkInfo(this.token)
        //   .then(res => {
        //     this.myName = res.name;
        //     console.log(this.myName);
        //   });
      })
      .catch(err => console.log(err));
  }

  getID(idMess: string) {
    let idProfile = idMess.split("_", 2);
    // console.log(idProfile);
    if (idProfile[1] > '1100000000000000') return 4;
    return idProfile[1];
  }

  getName(data: any) {
    if (data.name === undefined) {
      return data.senders.data[0].name;
    }
    return data.name;
  }

  checkActive(id, idevent) {
    if (id === idevent) return true;
    return false;
  }

  ScanInbox(id, $event) {
    // console.log(event);
    let k = (<HTMLInputElement>event.target).value;
    this.trangthai = this.checkActive(id, k);
    this.idclick = id;
    this.inboxReaderService.getMessages(this.token, id)
      .then(res => {
        // console.log(res);
        this.paging_next = res.paging.next;
        res = res.data;
        this.trangthai = true;
        this.messages = res;
        this.messages.reverse();
      })
      .catch(err => console.log(err));
  }

  ScanInboxOld() {
    this.inboxReaderService.getMessagesOld(this.paging_next)
      .then(res => {
        this.paging_next = res.paging.next;
        res = res.data;
        this.trangthai = true;
        let tmp = res.reverse();
        this.messages = tmp.concat(this.messages);
      })
      .catch(err => console.log(err));
  }

  GetTopRank() {
    if (this.rank_next === 'mducptit') {
      this.rank_next = 'https://graph.facebook.com/v3.0/me/threads/?fields=message_count&limit=500&access_token=' + this.token;
    }
    this.inboxReaderService.getTopRank(this.rank_next)
      .then(res => {
        this.rank_next = res.paging.next;
        this.rank = this.rank.concat(res.data);
      })
      .catch(err => console.log(err));
  }

  SapXep() {
    for (let i = 1; i <= 10; i++) {
      this.GetTopRank();
    }
    // for (const Xtmp of this.rank) {
    //   let idm = Xtmp.id.split("_", 2);
    //   if (idm[1] < '1100000000000000' && Xtmp.message_count > max){
    //     let max[] += Xtmp.message_count;
    //     let maxName[] += idm[1];
    //   }
    // }
    console.log(this.rank);
  }
}
