import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

@Injectable()
export class InboxReaderService {
  constructor(private http: Http) { }
  
  // checkInfo(token){
  //   const url = 'https://graph.facebook.com/me/?access_token=' + token;
  //   return this.http.get(url)
  //   .toPromise()
  //   .then(res => res.json());
  // }
  
  getListChat(token){
    const url = 'https://graph.facebook.com/v3.0/me/threads/?limit=10&fields=message_count,unread_count,name,senders&access_token=' + token;
    return this.http.get(url)
    .toPromise()
    .then(res => res.json());
  }

  getListChatMore(url){
    return this.http.get(url)
    .toPromise()
    .then(res => res.json());
  }

  getMessages(token,threadID){
    // tslint:disable-next-line:max-line-length
    const url = 'https://graph.facebook.com/v3.0/' + threadID + '/messages?fields=id,from,to,message,created_time,sticker,attachments{image_data,video_data,file_url,name},shares{id,link,name}&limit=10&access_token=' + token;
    return this.http.get(url)
    .toPromise()
    .then(res => res.json());
  }

  getMessagesOld(url){
    return this.http.get(url)
    .toPromise()
    .then(res => res.json());
  }

  getTopRank(url){
    // const url = 'https://graph.facebook.com/v3.0/me/threads/?fields=message_count&limit=500&access_token=' + token;
    return this.http.get(url)
    .toPromise()
    .then(res => res.json());
  }
}
