import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { Routes, RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { FriendsComponent } from './friends/friends.component';
import { FriendsService } from './friends/friends.service';
import { SignInComponent } from './sign-in/sign-in.component';
import { SignInService } from './sign-in/sign-in.service';
import { RoleListComponent } from './role-list/role-list.component';
import { RoleDetailComponent } from './role-detail/role-detail.component';
import { MainComponent } from './main/main.component';
import { TestComponent } from './test/test.component';
import { InboxReaderComponent } from './inbox-reader/inbox-reader.component';
import { InboxReaderService } from './inbox-reader/inbox-reader.service';

const routesCongig: Routes = [
  { path: '', redirectTo: 'main', pathMatch: 'full' },
  { path: 'sign-in', component: SignInComponent },
  { path: 'test', component: TestComponent },
  {
    path: 'main', component: MainComponent, children: [
      { path: 'friends', component: FriendsComponent },
      { path: 'role-list', component: RoleListComponent },
      { path: 'role-detail', component: RoleDetailComponent },
      { path: 'inbox-reader', component: InboxReaderComponent }
    ]
  },
  { path: '**', component: MainComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    FriendsComponent,
    SignInComponent,
    RoleListComponent,
    RoleDetailComponent,
    MainComponent,
    TestComponent,
    InboxReaderComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(routesCongig)
  ],
  providers: [
    FriendsService,
    SignInService,
    InboxReaderService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
