import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import * as $ from 'jquery';
import { FriendsService } from './friends.service';

@Component({
  selector: 'app-friends',
  templateUrl: './friends.component.html',
  styleUrls: ['./friends.component.css']
})
export class FriendsComponent implements OnInit {
  token = '';
  after = '';
  before = '';
  data: any;
  isLoading = false;

  constructor(private friendsService: FriendsService) { }

  ngOnInit() {
    // this.friendsService.getFriends(this.token)
    // .then(birthday => console.log(birthday))
    // .catch(err => console.log(err));
  }

  Rank(tmp: Number) {
    this.isLoading = true;
    console.log(event);
    if (tmp === 0) {
      this.friendsService.getFriendsBefore(this.token, this.before)
        .then(res => {
          this.data = res.data;
          console.log(this.data);
          this.after = res.paging.cursors.after;
          this.before = res.paging.cursors.before;
          this.isLoading = false;
        })
        .catch(err => {
          console.log(err);
          alert('Please Enter Your Access Token Full Right!');
        });
    } else {
      this.friendsService.getFriendsAfter(this.token, this.after)
        .then(res => {
          console.log(res);
          this.data = res.data;
          this.after = res.paging.cursors.after;
          this.before = res.paging.cursors.before;
          console.log(this.before);
          this.isLoading = false;
        })
        .catch(err => {
          console.log(err);
          alert('Please Enter Your Access Token Full Right!');
        });
    }

  }

  test($event){
    console.log(event);
    console.log(event.target);
  }

  // tslint:disable-next-line:one-line
  getMessage() {
    if (this.isLoading) { return 'LOADING...'; }
    return this.data;
  }

}
